import { Component } from '@angular/core';
import { User }    from './modal/user';
import { HttpService } from './services/http.service';
import {Observable} from 'rxjs/Rx';
import 'rxjs/add/observable/throw';
import 'rxjs/Rx';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [HttpService]
})
export class AppComponent {

constructor(private _httpService: HttpService) {
}

user = new User('', '', '', '');

submitted = false;

signUp() { 
    this.submitted = true; 
    let signupOperation:Observable<User[]>;
    console.log(this.user);
    

    signupOperation  = this._httpService.signUpUser(this.user)
    console.log("response" + signupOperation)
    
    signupOperation.subscribe(
                                users => {
                                      // Empty model
                                      this.user = new User('', '', '', '');
                                }, 
                                err => {
                                    // Log errors if any
                                    console.log(err);
                                });
    
 }

login() { 
    this.submitted = true; 
    let loginOperation:Observable<User[]>;
    console.log(this.user);
    loginOperation  = this._httpService.loginUser(this.user)
    console.log("response" + loginOperation)
    
    loginOperation.subscribe(
                                users => {
                                      // Empty model
                                      this.user = new User('', '', '', '');
                                }, 
                                err => {
                                    // Log errors if any
                                    console.log(err);
                                });
    
 }

}



